@import url(http://fonts.googleapis.com/css?family=Lato:400,700italic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.table {
	width: 100%;
	border: none;
	margin-bottom: 20px;
	text-align: center;

}
.table thead th {
	font-weight: bold;
	text-align: center;
	border: none;
	padding: 10px 15px;
	background: #d8d8d8;
	font-size: 14px;
	border-left: 1px solid #ddd;
	border-right: 1px solid #ddd;
}
.table tbody td {
	text-align: center;
	border-left: 1px solid #ddd;
	border-right: 1px solid #ddd;
	padding: 10px 15px;
	font-size: 14px;
	vertical-align: top;
}

.table tbody tr:nth-child(even){
	background: #f3f3f3;
}
* { padding: 0; margin: 0; }
body {  font-family: 'Lato', sans-serif; text-transform: uppercase; letter-spacing: 1px;}
 
.header {
    text-align: justify;
    height: 2em;
    padding: 2em 5%;
    background: #2c3e50;
    color: #fff;
}

.nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.nav a{
    text-decoration: none;
    color: rgb(255, 255, 255);
}

 


